h3 {
  font-size: 26pt;
  margin: 0;
  font-weight: 100;
}

.K {
  font-weight: 100;
  width: 100%;
  min-height: 100vh;
  font-size: 12pt;
  display: -webkit-box;
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  justify-content: flex-start;
  align-items: center;
  text-align: left;
  background: #FEFEFE;
}

.K-menu {
  width: 200px;
  height: 30px;
  display: -webkit-box;
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
  align-items: flex-end;
  justify-content: space-between;
  align-self: flex-end;
  font-size: 13pt;
  margin: 3vh 5%;
}

.K-menu a {
  color: #222;
  transition: all 0.2s;
  padding: 5px;
}

.K-menu a:hover {
  font-size: 16pt;
  color: #ee7e00;
}

.K-header {
  color: #222;
  width: 40%;
  margin: 20vh 10% 0 0;
  padding: 0 5px;
}

.K-header--nome {
  color: #ee7e00;
}

.K-header--itemlista {
  display: -webkit-box;
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
  align-items: center;
}

.K-header--itemlista i {
  margin-right: 10px;
  font-size: 8pt;
}

.K-hero {
  background: url("../public/imgs/bg1.jpg");
  background-size: cover;
  background-attachment: fixed;
  background-position: center;
  background-repeat: no-repeat;
  width: 100%;
  height: 20vh;
  margin: 10vh 0 0 0;
}

.K-trabalhos {
  width: 40%;
  margin: 10vh 10% 0 0;
  padding: 0 5px;
}

.K-trabalhos a {
  color: #333;
  text-decoration: none;
}

.K-trabalhos--container {
  display: -webkit-box;
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  margin-bottom: 10vh;
}

.K-trabalhos--container::after {
  content: "";
  flex: auto;
}

.K-rodape {
  height: 15vh;
  align-self: flex-end;
  display: flex;
  align-items: flex-end;
  padding: 0 15px 15px 0;
}

.K-rodape a {
  color: #5e5e5e;
  text-decoration: none;
  font-size: 10pt;
  transition: all 0.2s;
}

.K-rodape a:hover {
  color: #222;
}

@media all and (max-width: 1300px)
and (orientation: portrait) {
  .K-header,
  .K-trabalhos {
    width: 80%;
    margin-left: 5%;
  }
}

@media all and (max-width: 1300px)
and (orientation: landscape) {
  .K-header,
  .K-trabalhos {
    width: 60%;
  }
}