h4 {
    font-size: 16pt;
    margin-left: 0;
    margin-right: 0;
    font-weight: 400;
}

.K-trabalhos--trab {
    width: fit-content;
    max-width: 140px;
    transition: all 0.2s;
    padding: 5px;
    margin-bottom: 2vh;
}

.K-trabalhos--trab:hover {
    cursor: pointer;
    transform: scale(1.5);
    color: #ee7e00;
    background: #fff;
}

.K-trabalhos--trab--titulo {
    font-size: 14pt;
    text-decoration: underline;
    margin-bottom: 5px;
}

.K-trabalhos--trab--empresa {
    font-size: 10pt;
    margin: 0;
}

.K-trabalhos--trab--local {
    font-size: 10pt;
    margin: 0;
}

.K-trabalhos--trab--data {
    font-size: 10pt;
    margin: 0;
}

.K--trabalhos--categoria--titulo {
    text-align: center;
    margin: 0;
}